import { useEffect, useState, useCallback } from "react";
import { Routes, Route, Navigate, redirect } from "react-router-dom";
import "./App.css";
import Login from "./users/login";
import Register from "./users/register";
import Navbar from "./component/navbar";
import Home from "./users/home";
import AxiosInstance from "./config/Axios";




function App() {
  const [userToken, setToken] = useState(localStorage.getItem("token"));
  const [isAuth, setIsAuth] = useState(false);
  const [user, setUser] = useState(undefined)

   // check auth 
   //see https://react.dev/reference/react/useCallback
   //see https://dmitripavlutin.com/react-usecallback/
   const getAuthenticatedUser = useCallback(async () => {
    try {
      const res = await AxiosInstance.get("/user", { headers: { 
        Authorization: `Bearer ${userToken}` } });
        setUser(res.data)
    } catch (e) {
      console.log(e);
    }
  },[userToken])

  useEffect(() => {
    if (!userToken) {
      return;
    }

    getAuthenticatedUser();
    setIsAuth(true);
  }, [userToken, getAuthenticatedUser]);

  function logout() {
    localStorage.clear();
    setToken(undefined);
    setIsAuth(false);
    setUser(undefined)
    //nav to login
    return redirect("login");
  }

  function setAuthUser() {
    setIsAuth(true);
    setToken(localStorage.getItem("token"));
  }

  return (
    <div className="App">
      <Navbar isAuth={isAuth} logout={logout} />
      <Routes>
        <Route path="register" element={<Register />} />
        <Route path="login" element={<Login setAuthUser={setAuthUser} />} />
        <Route path="home" element={userToken ? <Home  user={user}/> : <Navigate to="/login" />} />
        <Route path="*" element={<NoPageFound />} />
      </Routes>
    </div>
  );
}

function NoPageFound() {
  return <div>Welcome to your 2nd HOME!</div>;
}

export default App;
