import React, { useState, useEffect, useCallback } from "react";
import AxiosInstance from "../config/Axios"
import CreateNote from "../component/note/createNote";
import Profile from "../component/profilePicture";
import NoteList from "../component/note/noteList";
import GetWeather from "../component/weather/getWeather";

export default function Home({ user }) {
  const [loading, setLoading] = useState(false);
  const [allnotes, setAllnotes] = useState([]);

  const getNotes = useCallback(async() => {
    await AxiosInstance.get("/notes", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    }).then((res) => {
      if (res.status === 200) {
        setLoading(false);
        setAllnotes(res.data.notes);
      }
    });
  },[])

  useEffect(() => {
    getNotes();
  }, [getNotes]);
  return (
    <div>
      <GetWeather />
      <Profile user={user} />
      <CreateNote getNotes={getNotes} />
      <NoteList user={user} loading={loading} allnotes={allnotes} getNotes={getNotes} />

    </div>
  );
}
