import { useState } from "react";
import AxiosInstance from "../config/Axios";
import { useNavigate } from "react-router-dom";
import {Button, Form} from 'react-bootstrap'
import CloudinaryUploadWidget from "../component/cloudinaryUpload";

export default function Register() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [company, setCompany] = useState("");
  const [imageUrl, setImageUrl] = useState("");

  const navi = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (username.length === 0 || company.length === 0 || password.length === 0) {
      alert("please fill in everything");
      return;
    }
    try {
      const response = await AxiosInstance.post("/register", {
        username: username,
        password: password,
        company: company,
        profilePicture: imageUrl,
      });
      console.log(response);
      navi("/home");
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <CloudinaryUploadWidget getImageUrl={setImageUrl} />
        <div>
          Username
          <Form.Control type="text" placeholder="username" onChange={(e) => setUsername(e.target.value)} />
        </div>
        <div>
          Password
          <Form.Control  type="password" placeholder="password" onChange={(e) => setPassword(e.target.value)} />
        </div>
        <div for="company">Choose who u work for:</div>
        <Form.Select name="company" id="company" onChange={(e) => setCompany(e.target.value)}>
          <option value="">Please select your company</option>
          <option value="Facebook">Facebook</option>
          <option value="Google">Google</option>
          <option value="Netflix">Netflix</option>
          <option value="General Assembly">General Assembly</option>
        </Form.Select>
        <Button type="submit">Register</Button>
      </form>
    </div>
  );
}
