import React, { useState } from "react";
import axios from "axios";
import { Form, Container, Row, Col } from "react-bootstrap";
import * as Icon from "react-bootstrap-icons";

export default function GetWeather() {
  const [data, setData] = useState({});
  const [location, setLocation] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const url = `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=metric&appid=fdb6259a1fa273a0b4cc5c76c114ed14`;

  const searchLocation = (event) => {
    if (event.key === "Enter") {
      axios
        .get(url)
        .then((response) => {
          setData(response.data);
          setLocation("");
        })
        .catch((err) => {
          setErrorMessage("Location not found!");
          setTimeout(() => {
            setErrorMessage("");
          }, 2000);
          setLocation("");
        });
    }
  };

  function weatherIcon(type) {
    if (type === "Clouds") {
      return <Icon.Cloud />;
    }

    if (type === "Rain") {
      return <Icon.CloudRain />;
    }
  }

  return (
    <Container>
      <Row>
        <Col className="mx-auto" md={5} sm={12}>
          <h2>Check the weather today!🌞</h2>
          <div className="search">
            <Form.Control
              value={location}
              onChange={(event) => setLocation(event.target.value)}
              onKeyPress={searchLocation}
              placeholder="Enter Location"
              type="text"
            />
          </div>
          {errorMessage && errorMessage}
          {data && (
            <div className="container">
              <div className="top">
                <div className="location">
                  <p>{data.name}</p>
                </div>
                <div className="temp">{data.main ? <h1>{data.main.temp.toFixed()}°C</h1> : null}</div>
                <div className="description">
                  {data.weather && Array.isArray(data.weather) && data.weather.length > 0 ? (
                    <p>
                      {data.weather[0].main}
                      {weatherIcon(data.weather[0].main)}
                    </p>
                  ) : null}
                </div>
              </div>
            </div>
          )}
        </Col>
      </Row>
    </Container>
  );
}
