import { useState, } from "react";
import AxiosInstance from "../../config/Axios";
import Loading from "../loading";
import {Button, Form, Container, Row, Col} from "react-bootstrap";

export default function CreateNote({getNotes}) {
  const [note, setNote] = useState({ noteDescription: "" });
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const newNoteItem = { ...note, [e.target.name]: e.target.value };
    setNote(newNoteItem);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    const token = localStorage.getItem("token");
    try {
      await AxiosInstance.post("/notes", note, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setLoading(false);
      setNote({ noteDescription: "" })
      getNotes()
    } catch (err) {
      alert(err);
      setLoading(false);
    }
  };

  return (
    <Container>
      <Row>
      {loading ? <Loading /> : ""}
     <Col md={5} sm={12} className={"mx-auto"}>
     <form onSubmit={handleSubmit}>
          <Form.Control style={{ height: '100px' }} className={"mb-2"} type="text" as="textarea" placeholder="Note description" onChange={handleChange} name="noteDescription" />
        <div className="d-grid gap-2">
          <Button variant="warning" type="submit">
            Create a note
          </Button>
        </div>
      </form>
     </Col>
      </Row>
    </Container>
  );
}
