import { NavLink } from "react-router-dom";
import { Navbar as BootStrapNavBar, Container, Nav } from "react-bootstrap";

export default function Navbar({ isAuth, logout }) {
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "numeric",
    minute: "numeric",
  };
  const today = new Date().toLocaleString(undefined, options);

  return (
    <div>
      <BootStrapNavBar expand={"lg"}>
        <Container>
          <BootStrapNavBar.Brand>Office App</BootStrapNavBar.Brand>
          <div>
            <p style={{ fontWeight: "light", fontStyle: "italic" }}>Today is {today}</p>
          </div>
          <Nav>
            {isAuth ? (
              <>
                <NavLink className={"nav-link"} to="/home">
                  Home
                </NavLink>
                <NavLink className={"nav-link"} to="/login" onClick={logout}>
                  Logout
                </NavLink>
              </>
            ) : (
              <>
                <NavLink className={"nav-link"} to="/login">
                  Login
                </NavLink>
                <NavLink className={"nav-link"} to="/register">
                  Register
                </NavLink>
              </>
            )}
          </Nav>
        </Container>
      </BootStrapNavBar>
    </div>
  );
}
