import { useState, useEffect } from "react";
import AxiosInstance from "../../config/Axios";
import Loading from "../loading";
import { Modal, Container, Button, Card, Row, Col, Form } from "react-bootstrap";
import { DateTime } from "luxon";
import InputGroup from "react-bootstrap/InputGroup";


export default function NoteList({ user, allnotes, getNotes, loading }) {
  const [selectedNoteForEdit, setSelectedNoteForEdit] = useState({});
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const [search, setSearch] = useState("");
  const [notes, setNotes] = useState(allnotes) 
  const handleChange = (e) => {
    const newNoteItem = { ...selectedNoteForEdit, [e.target.name]: e.target.value };
    setSelectedNoteForEdit(newNoteItem);
  };

  useEffect(()=>{
    const searchItemArr = allnotes.filter((note)=>{
      return note.noteDescription.toLowerCase().includes(search)
    })
    setNotes(searchItemArr)
  },[search,allnotes])
  

  function editNote(selectedNote) {
    setShow(true);
    setSelectedNoteForEdit(selectedNote);
  }
  const deleteNote = async (id) => {
    try {
      await AxiosInstance.delete(`/notes/${id}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      getNotes();
    } catch (e) {
      console.log(e);
    }
  };

  const patchNote = async () => {
    try {
      await AxiosInstance.patch(
        `/notes/${selectedNoteForEdit._id}`,
        { description: selectedNoteForEdit.noteDescription },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      getNotes();
      handleClose();
    } catch (e) {
      console.log(e);
    }
  };

  function timeAgo(date) {
    return DateTime.fromISO(date).toRelative();
  }

  return (
    <Container>
      {loading ? <Loading /> : ""}
      <InputGroup
        Placeholder
        xs={12}
        bg="Filter notes here"
        style={{
          margin: "15px",
        }}
        onChange={(e) => setSearch(e.target.value)}
        className="mb-3"
      >
        <InputGroup.Text id="inputGroup-sizing-default">Filter Note Description Here</InputGroup.Text>
        <Form.Control aria-label="Default" aria-describedby="inputGroup-sizing-default" />
      </InputGroup>

      <Row className="justify-content-center">
        {notes.map((note) => {
          const hasOwnerData = note.createdBy;
          const isOwnerOfNote = hasOwnerData && note.createdBy._id === user._id;
          return (
            <Col key={note._id} xs={12} md={6} lg={3} className="mt-3">
              <Card
                style={{
                  boxShadow: "1px 2px 9px #ffc008",
                }}
              >
                <Card.Body>
                  <Card.Title>{note.noteDescription}</Card.Title>
                  <div className="creator">
                    <p>{timeAgo(note.uploadDate)}</p>
                    {note.createdBy && (
                      <p>
                        {note.createdBy.username}, {note.createdBy.company}
                        <img height={30} src={note.createdBy.profilePicture} className="rounded" alt=""  style={{
          margin: "15px",
        }}/>
                      </p>
                    )}
                  </div>
                  {isOwnerOfNote ? (
                    <>
                      <Button variant="primary" onClick={() => editNote(note)}>
                        Edit
                      </Button>
                      <Button variant="primary" onClick={() => deleteNote(note._id)}>
                        Delete
                      </Button>{" "}
                    </>
                  ) : null}
                </Card.Body>
              </Card>
            </Col>
          );
        })}
      </Row>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Control value={selectedNoteForEdit.noteDescription} onChange={handleChange} name="noteDescription" />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={patchNote}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
}
